import React, { Component } from "react";
import ContactForm1 from "./ContactForm1";

class Contact1 extends Component {
  
  constructor() {
    super();
    this.state = {
      name: "React",
      showHideContactForm1: false
    };
    this.hideComponent = this.hideComponent.bind(this);
    this.state = { message: '', name: 'Name', email: 'email@example.com' };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  hideComponent(name) {
    console.log(name);
    switch (name) {
      case "showHideContactForm1":
        this.setState({ showHideContactForm1: !this.state.showHideContactForm1 });
        break;
      default:
        this.setState(null);
    }
  }

  handleChange(event) {
    this.setState({message: event.target.value})
  }

  handleSubmit (event) {
    event.preventDefault()

    const templateId = 'template_IClyReLt';
    this.sendFeedback(templateId, {message_html: this.state.message, from_name: this.state.name, reply_to: this.state.email})
    }
  
    sendFeedback (templateId, variables) {
      window.emailjs.send("mailgun", 'template_IClyReLt', variables, "user_DAxRhCUOWyZbMpVlmpKOk").then(res => {
        console.log('Email successfully sent!')
      })
      // Handle errors here however you like, or use a React error boundary
      .catch(err => console.error('Oh well, you failed. Here some thoughts on the error that occured:', err))
    }

  render() {
    const { showHideContactForm1 } = this.state;

    return ( 
    <div>
      <form id="contactForm1" className="contact_form text-left" onSubmit={this.handleSubmit}>
        <label htmlFor="cost" className="checkbox"><input type="checkbox" id="cost" value="cost" /> What does it cost?</label><br />
        <label htmlFor="type" className="checkbox"><input type="checkbox" id="type" value="type" /> Will this work for my type of business?</label> <br />
        <label htmlFor="cost" className="checkbox"><input type="checkbox" id="other" value="other" /> Other:</label><br />
        <textarea name="other" onChange={this.handleChange} value={this.state.message} style={{width: "100%"}}></textarea><br />
        {showHideContactForm1 && <ContactForm1 />}
      </form>
      <div className="text-center mx-auto">
      {!showHideContactForm1 && <button className="mx-auto contact-button" onClick={() => this.hideComponent("showHideContactForm1")}>
          Next
      </button>}
      <br />
      </div>
    </div>
    );
  }
}

export default Contact1;